@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
* {
  font-family: "Poppins", sans-serif !important;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html,
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.app-bar {
  background-color: white !important;
  box-shadow: none !important;
  border: none !important;
  border-bottom: none !important;
  padding: 4px 8px !important;
  position: fixed !important;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: auto;
  min-height: 60px !important;
  z-index: 1000;
}
.toolbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  gap: 15px;
  min-height: 52px !important;
  padding: 0 8px !important;
  position: relative;
}
.logo-box {
  padding: 5px;
}

.logo-box img {
  width: 50px;
  height: 42px;
}

.text-box {
  text-align: center;
}

.text-box p {
  color: black;
  font-size: 8px;
  margin: 0;
  line-height: 1.2;
}

.text-box .light {
  font-weight: 400 !important;
  font-size: 10px !important;
  font-style: italic !important;
}

.text-box .bold {
  font-weight: 600 !important;
}

.text-box .medium {
  font-weight: 500 !important;
  font-size: 10px !important;
  font-style: italic !important;
}

/* Target all Typography elements in text-box to override Material-UI defaults */
.text-box .MuiTypography-root {
  font-size: 10px !important;
  line-height: 1.2 !important;
}

/* Specific styling for DEPARTMENT OF LABOR AND EMPLOYMENT (second child) */
.text-box > .MuiTypography-root:nth-child(2) {
  font-size: 11px !important;
  font-style: normal !important;
  font-weight: bold !important;
}

/* Regional Office - italic (third child) */
.text-box > .MuiTypography-root:nth-child(3) {
  font-size: 10px !important;
  font-style: italic !important;
}

/* Cagayan De Oro - italic (fourth child) */
.text-box > .MuiTypography-root:nth-child(4) {
  font-size: 10px !important;
  font-style: italic !important;
  font-weight: normal !important;
}

.travel-system-text {
  font-size: 25px !important;
  font-weight: 700 !important;
  color: #212121 !important;
  white-space: nowrap;
  letter-spacing: 0.5px;
  font-family: "Poppins", sans-serif !important;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  margin: 0;
}

/* Target Material-UI Typography h1 variant specifically with high specificity */
.travel-system-text.MuiTypography-root.MuiTypography-h1,
.travel-system-text.MuiTypography-h1,
.MuiTypography-root.travel-system-text.MuiTypography-h1 {
  font-size: 25px !important;
  font-weight: 700 !important;
  line-height: 1.2 !important;
  color: #212121 !important;
}

.toolbar-right {
  display: flex;
  align-items: center;
  margin-left: auto;
  flex-shrink: 0;
  padding-left: 20px;
}

/* Responsive toolbar */
@media (max-width: 768px) {
  .toolbar {
    flex-wrap: wrap;
    justify-content: center;
    gap: 12px;
  }

  .toolbar-right {
    margin-left: 0;
    margin-top: 8px;
    padding-left: 0;
    width: 100%;
    justify-content: center;
  }

  .text-box {
    flex: 1;
    min-width: 0;
  }

  .admin-login-btn {
    font-size: 12px;
    padding: 8px 16px;
  }
}
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.scrollable-container {
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  min-height: 0;
  padding: 0;
  box-sizing: border-box;
}

.calendar {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-image: url(../assets/calendarbg.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: right;
  overflow: hidden;
}
.calendar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(25, 118, 210, 0.3) 0%, rgba(244, 67, 54, 0.3) 100%);
  z-index: 0;
}

.calendar > * {
  position: relative;
  z-index: 1;
  flex-shrink: 0; /* Prevent footer from shrinking */
}
