.refresh-btn {
  background: #4caf50;
  color: white;
  border: 1px solid #4caf50;
  padding: 8px 12px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.2s ease;
  letter-spacing: 0;
  white-space: nowrap;
  margin: 0;
  margin-right: 8px;
  font-family: "Poppins", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 40px;
